import { TweenMax, Elastic,TweenLite } from "gsap";
import React,{Component} from 'react';
import {Link} from 'react-router-dom';


 var canvas,container,ctx,sta,bounds,mx,my,dx,dy,particle,f,d,t,p,a,b,n,color,colo=220
 ;let particles=[];
 var THICKNESS = Math.pow( 15, 2 ),DRAG = 0.95, EASE = 0.25;
 var radius=50;
  class Particles2 extends Component{
    constructor(props){
        super(props);
       //this.app=app;
      //this.drawScene();
 //this.step();
    }
    render(){
        return(
        <div id="container">
        </div>);
    }
    componentDidMount(){
        this.drawScene();
    }
     drawScene(){sta=true;
        container=document.getElementById("container");
         canvas=document.createElement("canvas");
         ctx=canvas.getContext("2d");    
            canvas.height=img.height;
            canvas.width=img.width;
           ctx.drawImage(img,0,0);
           var da=ctx.getImageData(0,0,img.width,img.height);
            ctx.clearRect(0,0,img.width,canvas.height);
            for (let y = 0,y2=img.height; y < y2; y+=2) {
                for (let x = 0,x2=img.width; x <x2; x+=2) {
                    var a=Math.random()*Math.PI*2,
                    sin=Math.sin(a),
                    cos=Math.cos(a);
                  var  p={
                      ox:x,
                      oy:y,
                       vx:x,
                       vy:y,
                       x:x,
                       y:y,
                      color:"rgb("+da.data[(y*4*da.width)+(x*4)]+","+da.data[(y*4*da.width)+(x*4)+1]+","+da.data[(y*4*da.width)+(x*4)+2]+")",
                      speed: Math.random() * 4 + 2
                   };
                   /*TweenMax.to(p,p.speed,{
                       x:p.x,
                       y:p.y,
                       delay:y/170,
                       ease:Elastic.easeOut
                   });*/
                   TweenMax.to(p,2,{
                    x:20,
                    y:20,
                    delay:y/170,
                   // yoyoEase:true
                   });
                   particles.push(p);   

                }
                
            }
            ctx.clearRect(0, 0, canvas.width, canvas.height); 
            container.addEventListener('mousemove',function(e){
                bounds=canvas.getBoundingClientRect();
                mx=e.clientX-bounds.left;
                my=e.clientY-bounds.top;
            });container.appendChild(canvas);
            this.step();
    }
  step(){
      if(sta=!sta){console.log(mx);
        for (let i = 0;i<particles.length;i++) {
            p=particles[i];
            d=(dx=mx-p.x)*dx+(dy=my-p.y)*dy;
            f=-THICKNESS/d;
            if (d<THICKNESS) {
                t=Math.atan2(dy,dx);
                p.vx+=f*Math.cos(t);
                p.vy+=f*Math.sin(t);
            }
            p.x+=(p.vx*=DRAG)+(p.ox-p.x)*EASE;
            p.y+=(p.vy*=DRAG)+(p.oy-p.y)*EASE;
        }
      }
      else{ 
        ctx.clearRect(0, 0, canvas.width, canvas.height); 
        for (let i = 0,j=particles.length; i < j; i++) {
            p = particles[i];
            ctx.fillStyle=p.color;
            ctx.fillRect(p.x,p.y,1,1);
         }
        }
        requestAnimationFrame(()=>this.step());
       //requestAnimationFrame(this.step());
    }
}
export default Particles2;
