import React,{Component} from 'react';
import {InfoConsumer} from '../context';
import styled from 'styled-components';
import {TweenMax} from 'gsap';
import {Link} from 'react-router-dom';

var canvas,container,ctx,sta,bounds,mx,my,mx2,my2,dx,dy,particle,f,d,t,p,a,b,staloop=false,con,
n,color,da,colo=220,man=false
 ;let particles=[];
 var THICKNESS = Math.pow( 15, 3 ),THICKNESS2 = Math.pow( 15, 4.5 ),
 DRAG = 0.95, EASE = 0.25;
 var radius=50;



class Details extends Component{
  render(){
      
    return(
        <div>
            <header>
              <Link to='/Inicio' onClick={this.chasta} >
         <a className='nav-barlink' href="#">
        <svg style={{color:'white'}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 25.8 48.8"><polyline points="24.2 1.5 1.5 24.4 24.2 47.3" stroke-width="3" stroke-linecap="round" stroke-linejoin="round" stroke-miterlimit="10"></polyline></svg>
        </a>
       </Link>
   <div id='header-logo' style={{transform:"translate3d(0px,0px,0px)"},{opacity:"1"}}>
               
                    <div id='header-logo-bg'></div>
 <div id='header-logo-txt' style={{textAlign:'center'},{textAlign:'center'}}>portfolio</div>
    </div>
                <br></br>
    <div style={{color:"#717171"}}>
    <div style={{textAlign:"center"}}>
      P r o j e c t s
      <br></br>
      . . . . .
    </div>
    </div>
            </header>
            <div id="container"  align='center'>
           
        <InfoConsumer>
        {data => {
          const{
            id,
            headerTitle,
            headerSubtitle,
            headerText,
            img,
            title,
            maps,
            description,
            works,example1,example2,example3, lexample1,lexample2
          }=data.detailInfo;
          return(
        <div id='container2' style={{color:'#717171'}}>{headerSubtitle}
            <div className='project-content' style={{cursor:'pointer'}}>
            <h4 className='project-content-title'>{headerTitle}</h4>
          <p className='project-content-desc'>{description}</p>
        <h3>{works}</h3>
          <a href={lexample1} target="_blank">{example1}</a>
          <br></br>
        <a href={lexample2} target="_blank">{example2}</a>
        <br></br>
          <a href='#'>{example3}</a>
        </div>
        </div>
          );
        }}
        </InfoConsumer>
        </div>
        </div>
       );
      }
      chasta(){
        staloop=true;
      }
      componentDidMount(){
          this.drawScene();
      }
      drawScene(){
        console.log(con++);
        sta=true;staloop=false;
        container=document.getElementById("container");
         canvas=document.createElement("canvas");
         ctx=canvas.getContext("2d");    
            canvas.height=img.height;
            canvas.width=img.width;
           ctx.drawImage(img,0,0);
            da=ctx.getImageData(0,0,img.width,img.height);
            ctx.clearRect(0,0,canvas.width,canvas.height);
            for (let y = 0,y2=img.height; y < y2; y+=2) {
                for (let x = 0,x2=img.width; x <x2; x+=2) {
                    var a=Math.random()*Math.PI*2,
                    sin=Math.sin(a),
                    cos=Math.cos(a);
                  var  p={
                      ox:x,
                      oy:y,
                       vx:x,
                       vy:y,
                       x:x,
                       y:y,
                      color:"rgb("+da.data[(y*4*da.width)+(x*4)]+","+da.data[(y*4*da.width)+(x*4)+1]+","+da.data[(y*4*da.width)+(x*4)+2]+")",
                      speed: Math.random() * 4 + 2
                   };
                   /*TweenMax.to(p,p.speed,{
                       x:p.x,
                       y:p.y,
                       delay:y/170,
                       ease:Elastic.easeOut
                   });*/
                   TweenMax.to(p,2,{
                    x:20,
                    y:20,
                    delay:y/170,
                   // yoyoEase:true
                   });
                   particles.push(p);   

                }
                
            }
            ctx.clearRect(0, 0, canvas.width, canvas.height); 
            container.addEventListener('mousemove',function(e){
                bounds=canvas.getBoundingClientRect();
                mx=e.clientX-bounds.left;
                my=e.clientY-bounds.top;
            });container.appendChild(canvas);
            this.step();
    }
  step(){
      if(sta=!sta){
        if ( !man ) {

            t = 200;
            mx2 = da.width * .65 + ( Math.cos( t * 2.1 ) * Math.cos( t * 0.9 ) * da.width * 0.45 );
            my2 = da.height * .6 + ( Math.sin( t * 3.2 ) * Math.tan( Math.sin( t * 0.8 ) ) * da.height * 0.45 );
          }
        for (let i = 0;i<particles.length;i++) {
            p=particles[i];
            d=(dx=mx2-p.x)*dx+(dy=my2-p.y)*dy;
            f=-THICKNESS2/d;
            if (d<THICKNESS2) {
                t=Math.atan2(dy,dx);
                p.vx+=f*Math.cos(t);
                p.vy+=f*Math.sin(t);
            }
            p.x+=(p.vx*=DRAG)+(p.ox-p.x)*EASE;
            p.y+=(p.vy*=DRAG)+(p.oy-p.y)*EASE;
        }
        for (let i = 0;i<particles.length;i++) {
            p=particles[i];
            d=(dx=mx-p.x)*dx+(dy=my-p.y)*dy;
            f=-THICKNESS/d;
            if (d<THICKNESS) {
                t=Math.atan2(dy,dx);
                p.vx+=f*Math.cos(t);
                p.vy+=f*Math.sin(t);
            }
            p.x+=(p.vx*=DRAG)+(p.ox-p.x)*EASE;
            p.y+=(p.vy*=DRAG)+(p.oy-p.y)*EASE;
        }
      }
      else{ 
        ctx.clearRect(0, 0, canvas.width, canvas.height); 
        for (let i = 0,j=particles.length; i < j; i++) {
            p = particles[i];
            ctx.fillStyle=p.color;
            ctx.fillRect(p.x,p.y,1,1);
         }
        }
        //
        if(staloop){container.removeChild(canvas);
        particles=[]; }
        if(staloop)return;
        requestAnimationFrame(()=>this.step());
    }
}
export default Details;
var img=new Image();
 //img.onload=this.drawScene;
img.src ="data:image/jpeg;base64,";