import React,{component, Component} from 'react';

 class Particles extends Component{
    

    constructor(props){
      //  this.app=app;
super(props);
      // img.onload=this.drawScene;
    }
    render(){
        return(
            <div></div>
        );
    }
    componentDidMount(){
        this.drawScene();
    }
     drawScene(){
       
var canvas=document.getElementById("canvas");
       // var canvas=document.createElement("canvas");
        var ctx=canvas.getContext("2d");
        var particles=[]; 
    
            canvas.height=img.height;
            canvas.width=img.width;
            ctx.drawImage(img,0,0);
            var data=ctx.getImageData(0,0,img.width,img.height);
            ctx.clearRect(0,0,canvas.width,canvas.height);
    
            for (let y = 0,y2=data.height; y < y2; y+=2) {
                for (let x = 0,x2=data.width; x <x2; x+=2) {
                   var particle={
                       xx:x,
                       yy:y,
                       xx2:img.width/2,
                       yy2:img.height/2,
                       color:"rgb("+data.data[(y*4*data.width)+(x*4)]+","+data.data[(y*4*data.width)+(x*4)+1]+","+data.data[(y*4*data.width)+(x*4)+2]+")"
                      // speed:Math.random() *4
                   };
                    particles.push(particle);
                }
                
            }
           
            ctx.clearRect(0, 0, canvas.width, canvas.height);

            //ctx.fillStyle="white";
            for (let i = 0,j=particles.length; i < j; i++) {
                var particle = particles[i];
                ctx.fillStyle=particle.color;
                ctx.fillRect(particle.xx,particle.yy,1,1);
            }
    }
    
}
export default Particles;
var img=new Image();
 //img.onload=this.drawScene;
img.src ="data:image/png;base64,";